import humanizeDuration from 'humanize-duration';

import { Typography, Autocomplete, Avatar, Grid, TextField, Button, Link } from '@mui/material';
import MainCard from 'ui-component/cards/MainCard';
import { useState, useEffect, useRef } from 'react';
import { Box } from '@mui/system';

async function getChannel(id) {
  const response = await fetch(`https://api.twitch.tv/helix/users?id=${encodeURIComponent(id)}`, {
    responseType: 'json',
    throwHttpErrors: false,
    headers: {
      'Client-Id': '9uavest5z7knsvpbip19fxqkywxz3ec'
    }
  });

  const data = await response.json();

  if (!response.ok) {
    throw new Error(data.message ? `${data.error}, ${data.message}` : response.statusText);
  }

  if (!data.data[0]) {
    throw new Error('Channel not found.');
  }

  return data.data[0];
}

function humanizeLong(ms) {
  return humanizeDuration(Date.now() - ms, {
    units: ['y', 'mo', 'd', 'h', 'm', 's'],
    largest: 4,
    round: true,
    spacer: ' ',
    conjunction: ' and ',
    serialComma: false,
    zero: '0'
  });
}

const Page = () => {
  const [outputData, setOutputData] = useState(null);
  const [outputError, setOutputError] = useState(null);
  const [searchResults, setSearchResults] = useState([]);
  const [channel, setChannel] = useState(null);
  const inputTimerRef = useRef(null);

  useEffect(() => {
    document.title = 'LsCo Software - UserInfo';
  }, []);

  useEffect(() => {
    if (!channel) return;

    const fetchData = async () => {
      try {
        const data = await getChannel(channel.name.split(' ')[3]);
        setOutputError(null);
        setOutputData(data);
      } catch (err) {
        setOutputError(err.message);
        setOutputData(null);
      }
    };
    fetchData();
  }, [channel]);

  const handleSearchChange = async (value) => {
    clearTimeout(inputTimerRef.current);

    if (value.trim() !== '') {
      inputTimerRef.current = setTimeout(async () => {
        const response = await fetch(`https://api.twitch.tv/helix/search/channels?query=${value}&first=25`, {
          headers: {
            'Client-Id': '9uavest5z7knsvpbip19fxqkywxz3ec'
          }
        });

        const data = await response.json();

        if (response.ok) {
          setSearchResults(
            data.data.map((channel) => {
              return {
                name: `${new RegExp('^\\w+$').test(channel.display_name) ? channel.display_name : channel.broadcaster_login} - ID: ${
                  channel.id
                }`,
                avatarUrl: channel.thumbnail_url.replace('300x300', '50x50')
              };
            })
          );
        } else {
          setSearchResults([]);
        }
      }, 500);
    } else {
      setSearchResults([]);
    }
  };

  const handleSearchChangeID = async (value) => {
    clearTimeout(inputTimerRef.current);

    if (value.trim() !== '') {
      inputTimerRef.current = setTimeout(async () => {
        const fetchData = async () => {
          try {
            const data = await getChannel(value);
            setOutputError(null);
            setOutputData(data);
          } catch (err) {
            setOutputError(err.message);
            setOutputData(null);
          }
        };
        fetchData();
      }, 500);
    }
  };

  return (
    <MainCard title="UserInfo" description="Here you can get a lot of info about a Twitch user.">
      <Grid container spacing={2} alignItems="center">
        <Grid item xs={6}>
          <Autocomplete
            options={searchResults}
            getOptionLabel={(option) => option.name}
            onChange={(event, value) => {
              setChannel(value);
            }}
            renderInput={(params) => (
              <TextField
                {...params}
                fullWidth
                id="standard-basic"
                label="Channel by Name"
                variant="filled"
                onChange={(event) => {
                  handleSearchChange(event.target.value);
                }}
              />
            )}
            renderOption={(props, option) => (
              <li {...props}>
                <Box display="flex" alignItems="center">
                  <Avatar src={option.avatarUrl} alt={option.name} sx={{ width: 25, height: 25, marginRight: 1 }} />
                  {option.name}
                </Box>
              </li>
            )}
          />
        </Grid>
        <Grid item xs={6}>
          <TextField
            fullWidth
            id="standard-basic"
            label="Channel by ID"
            variant="filled"
            onChange={(event) => {
              handleSearchChangeID(event.target.value);
            }}
          />
        </Grid>
        <Grid item xs={12}>
          {outputError && (
            <Typography variant="body2" style={{ color: 'red', marginTop: '10px', textAlign: 'center' }}>
              {outputError}
            </Typography>
          )}
          {outputData && (
            <Box
              sx={{
                backgroundColor: 'rgb(250,250,250,0.1)',
                borderRadius: '5px',
                padding: '15px',
                whiteSpace: 'pre-wrap',
                maxWidth: '100%',
                overflowX: 'auto'
              }}
            >
              <Typography variant="body2" component="pre">
                <strong>id:</strong> {outputData.id}
                <br />
                <strong>login:</strong> {outputData.login}
                <br />
                <strong>display_name:</strong> {outputData.display_name}
                <br />
                <strong>type:</strong> {outputData.type === '' ? 'default' : outputData.type}
                <br />
                <strong>broadcaster_type:</strong> {outputData.broadcaster_type === '' ? 'default' : outputData.broadcaster_type}
                <br />
                <strong>description:</strong> {outputData.description === '' ? 'default' : outputData.description}
                <br />
                <strong>profile_image_url:</strong>{' '}
                {outputData.profile_image_url === '' ? (
                  'default'
                ) : (
                  <Link target="_blank" href={outputData.profile_image_url}>
                    {outputData.profile_image_url}
                  </Link>
                )}
                <br />
                <strong>offline_image_url:</strong>{' '}
                {outputData.offline_image_url === '' ? (
                  'default'
                ) : (
                  <Link target="_blank" href={outputData.offline_image_url}>
                    {outputData.offline_image_url}
                  </Link>
                )}
                <br />
                <strong>created_at:</strong> {outputData.created_at} ({humanizeLong(new Date(outputData.created_at).getTime())} ago)
              </Typography>
              <Button
                variant="contained"
                color="primary"
                href={`https://www.twitch.tv/${outputData.login}`}
                target="_blank"
                rel="noopener noreferrer"
                style={{ marginTop: '10px' }}
              >
                Visit Channel
              </Button>
            </Box>
          )}
        </Grid>
      </Grid>
    </MainCard>
  );
};

export default Page;
